import { useState } from 'react';
import { Input, Textarea } from "../components";
import {getAuthGroup, sendMessage} from "../utils";
import Swal from "sweetalert2";
import { Groups } from "../data/";

const categories = ["", "", ""];

export default function ScorePage() {
    const groupData = getAuthGroup();

    const [writer, setWriter] = useState('');
    const [targetGroup, setTargetGroup] = useState('');
    const [description, setDescription] = useState('');
    const [category, setCategory] = useState(''); //   
    const [isLoading, setIsLoading] = useState(false);
    const GOOGLE_APP_SCRIPT_URL = "https://script.google.com/macros/s/AKfycby75qEgVWuDfr7DNTBIwYCvgNVfAffB3rEKcyX9qUAdc6WbZK7sUr-nglxO2KVRtJM9pQ/exec";

    const handleSubmit = () => {
        if (!writer || !targetGroup || !description || !category) {
            Swal.fire("", ",   ", "error");
            return;
        }

        const message = `
<b> </b>

<b> :</b> ${groupData.name}
<b> :</b> ${writer}
<b> :</b> ${targetGroup}
<b>:</b> ${category}

<b>   ?</b> 
${description}

#__${new Date().getDate()}
#_${category.replace(/\s+/g, '').toLowerCase()}_${new Date().getDate()}
#_${targetGroup.replace(/\s+/g, '').toLowerCase()}_${new Date().getDate()}
#_${category.replace(/\s+/g, '').toLowerCase()}_${targetGroup.replace(/\s+/g, '').toLowerCase()}_${new Date().getDate()}
        `;

        Swal.fire({
            title: " ?",
            icon: "question",
            showCancelButton: true,
            confirmButtonText: "",
            cancelButtonText: "",
            confirmButtonColor: "#000",
        }).then((res) => {
            if (res.isConfirmed) {
                sendMessage(message);
                setCategory("");
                setTargetGroup("");
                setDescription("");
                setIsLoading(true);

                const payload = {
                    writer: writer,
                    targetGroup: targetGroup,
                    description: description,
                    category: category,
                    date: new Date().getDate() + "." + new Date().getMonth() + "." + new Date().getFullYear(),
                };
    
                //  POST-  URL  Google Apps Script
                fetch(GOOGLE_APP_SCRIPT_URL, {
                    method: 'POST',
                    // : Apps Script     POST,     redirect  text/plain
                    mode: 'no-cors', //    CORS-   POST
                    redirect: 'follow',
                    headers: {
                        // Content-Type ,   Apps Script    'application/json'  CORS
                    },
                    body: JSON.stringify(payload),
                })
                .then(() => {
                    const now_date = new Date().getDate();
                    // : ..    ,       .
                    //    ,     ,   .
                    if (localStorage.getItem(`amountOfVotes-${now_date}`) !== null) {
                        localStorage.setItem(`amountOfVotes-${now_date}`, Number(localStorage.getItem(`amountOfVotes-${now_date}`)) + 1);
                    } else {
                        localStorage.setItem(`amountOfVotes-${now_date}`, 1);
                    }
                    Swal.fire("!", "     !", "success");
                    
                    //   
                    setWriter('');
                    setTargetGroup('');
                    setDescription('');
                    setCategory('');
                })
                .catch(error => {
                    console.error("   :", error);
                    Swal.fire("!", "    .", "error");
                })
                .finally(() => {
                    setIsLoading(false);
                });
            }
        });
    };

    const otherGroups = Groups.filter(group => group.name !== groupData?.name);

    return (
        localStorage.getItem(`amountOfVotes-${new Date().getDate()}`) >= 3 ? 
            (<div className="container text-3xl mx-auto py-6 text-center">
                     
            </div>
            ) : (
            <div className="container max-w-xl mx-auto py-6">
            <h1 className="text-2xl font-bold mb-4"> </h1>

            {/*  input     */}
            <div className="mb-6">
                <h5 className="font-medium mb-2">,   :</h5>
                <input
                    type="text"
                    value={groupData?.name || " "}
                    disabled
                    className="w-full px-3 py-2 border border-gray-300 bg-gray-100 text-gray-700 rounded-xl cursor-not-allowed"
                />
            </div>

            <div className="mb-6">
                <h5 className="font-medium mb-2"> ?</h5>
                <Input
                    label=" "
                    value={writer}
                    onChange={(e) => setWriter(e.target.value)}
                    id="writer"
                    name="writer"
                />
            </div>

            <div className="mb-6">
                <h5 className="font-medium mb-2">  ?</h5>
                <select
                    value={targetGroup}
                    onChange={(e) => setTargetGroup(e.target.value)}
                    className="w-full px-3 py-2 border border-gray-300 rounded-xl"
                >
                    <option value=""> </option>
                    {otherGroups.filter(group => group.name !== "").map((group, index) => (
                        <option key={index} value={group.name}>{group.name}</option>
                    ))}
                </select>
            </div>

            <div className="mb-6">
                <h5 className="font-medium mb-2"> </h5>
                <select
                    value={category}
                    onChange={(e) => setCategory(e.target.value)}
                    className="w-full px-3 py-2 border border-gray-300 rounded-xl"
                >
                    {categories.map((cat, index) => (
                        <option key={index} value={cat}>
                            {cat}
                        </option>
                    ))}
                </select>
            </div>

            <div className="mb-6">
                <h5 className="font-medium mb-2">   ?</h5>
                <Textarea
                    label=",      <3"
                    value={description}
                    onChange={(e) => setDescription(e.target.value)}
                    id="description"
                    name="description"
                />
            </div>

            <button
                onClick={handleSubmit}
                className="bg-black text-white px-6 py-2 rounded-xl hover:bg-gray-800 transition"
            >
                
            </button>
            </div>
        )
    );
}
