import { useState } from 'react';
import { Input, Textarea } from "../components";
import {getAuthGroup, sendMessage} from "../utils";
import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";

const questions = [
    "",
    "",
    "",
    "    ?",
    "   ?",
    "    ?",
    "   ?",
    "   ?",
    "     ?",
    "  ?",
    "       ?",
    "      ?",
];

export default function FeedbackPage() {
    const [username, setUsername] = useState('');
    const [answers, setAnswers] = useState(Array(questions.length).fill(''));

    const groupData = getAuthGroup();

    const navigate = useNavigate();

    const handleTextareaChange = (index, value) => {
        const newAnswers = [...answers];
        newAnswers[index] = value;
        setAnswers(newAnswers);
    };

    const handleSubmit = () => {
        if (!username.trim()) {
            return Swal.fire({
                title: " !",
                text: ",   .",
                icon: "error",
                confirmButtonColor: "#000",
            });
        }

        const emptyIndex = answers.findIndex(answer => !answer.trim());
        if (emptyIndex !== -1) {
            return Swal.fire({
                title: " !",
                text: `,   : "${questions[emptyIndex]}"`,
                icon: "error",
                confirmButtonColor: "#000",
            });
        }

        let message = `<b>  </b>\n`;
        message += `<b>:</b> ${groupData?.name || ""}\n`;
        message += `<b>:</b> ${username}\n\n`;

        questions.forEach((question, index) => {
            message += `<b>${question}</b>\n${answers[index]}\n\n`;
        });

        Swal.fire({
            title: " ?",
            text: "   ?",
            icon: "warning",
            showCancelButton: true,
            confirmButtonColor: "#000",
            cancelButtonColor: "#909090",
            confirmButtonText: "",
            cancelButtonText: "",
        }).then((result) => {
            if (result.isConfirmed) {
                sendMessage(message)
                    .then(() => {
                        navigate(-1);
                        Swal.fire({
                            title: "!",
                            text: "   !",
                            confirmButtonText: "",
                            confirmButtonColor: "#000",
                            icon: "success"
                        });
                    });
            }
        });
        message += `#${groupData?.name.replace(/\s+/g, '_').toLowerCase() || ""}`
    };

    return (
        <div className="container max-w-xl mx-auto py-6">
            <h1 className="text-2xl font-bold"> ?</h1>
            <p className="mb-6">    !</p>

            <div className="mb-4">
                <h5 className="font-medium mb-2"> </h5>
                <Input
                    label=""
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                    id="username"
                    name="username"
                />
            </div>

            {questions.map((question, index) => (
                <div className="mb-4" key={index}>
                    <h5 className="font-medium mb-2">{question}</h5>
                    <Textarea
                        label=" "
                        value={answers[index]}
                        onChange={(e) => handleTextareaChange(index, e.target.value)}
                        id={`textarea-${index}`}
                        name={`textarea-${index}`}
                    />
                </div>
            ))}

            <button
                onClick={handleSubmit}
                className="bg-black text-white px-6 py-2 rounded-xl hover:bg-gray-800 transition"
            >
                 
            </button>
        </div>
    );
}
